@import url('https://fonts.googleapis.com/css2?family=Karla:wght@300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Markazi+Text:wght@400;500;600;700&display=swap');

:root {
  --primary-green: #495E57;
  --primary-yellow: #F4CE14;
  --secondary-gray: #EDEFEE;
  --secondary-koral: #EE9972;
  --secondary-font-fam: Karla, sans-serif;
  --primary-font: 'Markazi Text', serif;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

#root {
  padding: 25px 0px 0px 0px;
  display: flex;
  flex-direction: column;
  width: 100vw;
}

h1 {
  font-family: var(--primary-font);
  font-weight: 500;
  font-size: 64px;
  margin: 0;
  line-height: 0.6;
}

h2 {
  font-family: var(--primary-font);
  font-size: 40px;
  font-weight: 400;
  margin: 0;
}

.nav-item,
a {
  font-family: var(--secondary-font-fam);
  color: var(--primary-green);
  text-decoration: none;
}

a:visited {
  color: var(--primary-yellow);
}

li:hover {
  transform: scale(1.2);
}

.underline {
  border-bottom: 2px solid var(--secondary-koral);
}

p {
  font-weight: 700;
  font-family: var(--secondary-font);
  font-size: 18px
}

img {
  width: 100px;
  height: 100px;
  border-radius: 15px;
}

button {
  background-color: var(--primary-yellow);
  border-radius: 15px;
  width: 180px;
  height: 40px;
  text-align: center;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: var(--secondary-koral);
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.contact-info li {
  font-weight: 400;
  line-height: 20px;
}

@media only screen and (max-width: 762px) {

  #root {
    align-items: center;
    justify-content: center;
    padding: 25px 0px 15px 0px;
    height: 100%;
  }
}

@media only screen and (min-width: 763px) and (max-width: 1064px) {

  #root {
    align-items: center;
    justify-content: center;
  }
}